.title-global {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    font-family: 'BERNIERDistressed';
}

.main-title {
    display: flex;
    height: 100px;
    width: 80%;
    justify-content: center;
    padding-top: 15px;
    font-size: 55px;
    background-color: rgb(236, 236, 236);
    border-bottom: 2px solid black;
    margin-bottom: 40px;

}

.time {
    display: block;
    position: absolute;
    font-size: 14px;
    right: 10%;
    bottom: 45px;
    
}