.title-text::first-letter,
.news-text-conteiner::first-letter,
.comments-text::first-letter,
.comments-name::first-letter {
    text-transform: uppercase;
}

main {
    background-color: rgb(236, 236, 236);
}

/* ==================================Независимый контейнер ЗАГОЛОВОК======================== */
* {
    box-sizing: border-box;
    font-family: "Roboto", Tahoma;
}


/* ==================================Главный контейнер НОВОСТЕЙ и ВИДЖЕТА======================== */
.common-backdrop {
    display: flex;
    justify-content: center;
    background-color: rgb(236, 236, 236);
    width: 100vw;
    gap: 0px 20px;
    /* ==========del&all============ */
}

/* ==================================Главный контейнер НОВОСТЕЙ и ВИДЖЕТА rjytw======================== */
/* ==================================контейнер Новости======================== */
.news {
    display: flex;
    width: 600px;
    /* flex-flow: row wrap; */
    flex-direction: column;
    gap: 30px;
}

.one-news-container {
    display: flex;
    flex-direction: column;
    /* top: 50px; */
    background-color: rgb(255, 255, 255);
    padding: 20px;
    border-radius: 10px;
    color: #000;
    text-decoration: none;
}


.author-text {
    font-size: 18px;
    font-weight: bold;
    border-bottom: 2px solid #000;
}

.title-text {
    font-size: 22px;
    font-weight: bold;
    padding: 15px 0px 15px 0px;
}

.news-text-conteiner {
    font-size: 17px !important;
}


.icon-from-liba, .icon-from-liba_2 {
    display: flex;
    width: 150px;
    justify-content: space-between;
    margin-top: 20px;
    font-size: 17px;

}
.icon-for-hover {
    display: flex;
}

.footer-icon-all {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 25px;
    width: 25px;
}

.footer-icon-all:hover {
    height: 25px;
    width: 25px;
    opacity: 0.9;
    background-color: rgb(252, 179, 179);
    border-radius: 50px;
    cursor: pointer;
}



.сomment-counter {
    color: #000000;
    font-weight: bold;
    margin-left: 10px;
}