.author {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;


    position: sticky;
    top: 15px;
    background-color: rgb(252, 250, 248);
    height: 90vh;
    width: 200px;
    border-radius: 10px;
    font-size: 16px;
    font-family: 'BERNIERRegular';
}

.news-authors {
    font-size: 15px;
    border-bottom: 0px solid black;
}

.link {
    font-size: 16px;
    color: #000;
}

.link:hover {
    color: brown;
}

@media screen and (max-width: 690px) {
    .link {
        font-size: 13px;
    }

    .author {
        align-items:baseline;
        padding-left: 10px;
        margin-right: 20px;
    }
}