.specific-news-container {
    display: flex;
    flex-direction: column;
    /* top: 50px; */
    background-color: rgb(255, 255, 255);
    padding: 20px;
    border-radius: 10px;
    color: #000;
    text-decoration: none;
}
.spe-icon-all {
    font-size: 16px;
}

.icon-subscribe{
margin-right: 5px;
}

.spe-icon-all:hover {
    color: blue;
    font-size: 17px;
    cursor: pointer;
}

/* ==================================Автор и Заголовок======================== */
.specific-one-news-container {
    display: flex;
    position: relative;
}

.div-for-icon-and-name {
    display: flex;
}

.icon-subscribe {
    position: absolute;
    right: 20px;
}

.icon-additionally {
    position: absolute;
    right: 0px;
}

/* ==================================Автор и Заголовок конец======================== */
/* ================================== Начало текст======================== */
.title-text {
    font-size: 22px;
    font-weight: bold;
    padding: 15px 0px 15px 0px;
}

.news-text-conteiner {
    font-size: 17px;
}

