/* ==================================Виджеты +ховер(сердечко)======================== */

/* ==================================низ Виджитов======================== */

.div-for-input-and-btn {
    display: flex;
    position: relative;
}

.textarea {
    height: 100px;
    width: 90%;
    padding: 15px 130px 15px 15px;
    border: none;
    resize: none;
    overflow: hidden;
    position: relative;
    border-radius: 10px;
    margin: 20px;
    border: 0px;
    background-color: #f0efef;
    transition: height 0.3s ease-in-out;
    max-height: 500px;
}

.textarea ::-webkit-scrollbar {
    width: 0px;
}
/* ==================================Кнопка======================== */
.btn-send-in-textarea {
    position: absolute;
    height: 40px;
    width: 112px;
    background-color: #4683d9;
    color: #f0efef;
    border-radius: 10px;
    text-align: center;
    padding-top: 10px;
    font-size: 16px;
    z-index: 999;
    right: 40px;
    bottom: 25px;
    cursor: pointer;
}
.btn-send-in-textarea:hover {
    background-color: #578fdd;
}
.btn-send-in-textarea:active {
    background-color: #578fdd;
}


/* ==================================Иконка Автор Коментарий======================== */
.comments-container {
    min-height: 150px ;
    margin: 10px;
    position: relative;

}

.image-icon {
    height: 40px;
    width: 40px;
    border-radius: 50px;
    background-color: rgb(177, 188, 248);
    text-align: center;
    padding: 10px;
}

.comments-name {
    padding: 5px;
    font-weight: bold;
}

.comments-name:hover {
   color: #4683d9;
   cursor: pointer;
}

.comments-text {
    padding: 5px;
    margin-top: 10px;
}

.answer {
    position: absolute;
    right: 5px;
    width: 100px;
    text-align: right;
    padding: 5px;
    font-weight: bold;
    color: #8a8a8a;
    cursor: pointer;
    width: 100px;
}

.answer:hover {
    color: #f31010;
}



@media screen and (max-width: 860px) {
    .specific-news-container {
        margin-left: 10px;
    }
}